'use strict';

var React = require('react'),
    CounterForm = require('../CounterForm'),
    CounterList = require('../CounterList');

module.exports = React.createClass({

    render: function () {
        return (
            <div className="counterbox">
                <CounterForm />
                <CounterList counters={this.props.counters}/>
            </div>
        );
    }
});



